/* Breakpoint values */
/* = Layout = */
section {
  height: auto;
  min-height: 100vh;
}

section > div {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
  width: 100%;
  position: relative;
}

nav {
  background-color: #232323;
  display: none;
  font-size: 0.9em;
  height: 50px;
  letter-spacing: 0.08em;
  position: fixed;
  width: 100%;
  z-index: 5;
}

nav .logo {
  left: 10px;
  position: absolute;
  top: 5px;
  display: inline-block;
}

nav a {
  color: rgba(255, 255, 255, 0.7);
  display: block;
  height: 100%;
  padding: 15px 15px 0;
  text-decoration: none;
  width: 100%;
}

nav > div {
  height: 100%;
  left: 0;
  margin: 0 auto;
  max-width: 1500px;
  right: 0;
}

nav .active {
  background-color: #FFF;
}

nav .active a {
  color: #232323 !important;
}

nav ul {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: flex-end;
  z-index: 20;
}

nav li {
  height: 100%;
}

nav li:hover a {
  color: #FFF;
}

.intro {
  background-color: #F49206;
}

.intro-info {
  align-items: flex-end;
  display: flex;
  flex-flow: row nowrap;
  height: 100vh;
  justify-content: space-between;
  padding: 0 5px;
}

.intro-info__when, .intro-info__where {
  margin: 0 0 30px 20px;
}

.intro-info__when p:first-child, .intro-info__where p:first-child {
  font-size: 1.5em;
  letter-spacing: 0.3em;
  margin-bottom: 20px;
  text-transform: uppercase;
}

.intro-info__when p:not(:first-child), .intro-info__where p:not(:first-child) {
  color: #FFF;
  font-size: 1.1em;
  letter-spacing: 0.15em;
  margin-bottom: 10px;
}

.intro-info__logo {
  bottom: -6px;
  height: auto;
  left: 0;
  margin: 0 auto;
  min-width: 300px;
  position: absolute;
  right: 0;
  width: 40%;
}

.about {
  background-color: #232323;
}

.about-content {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
}

.about-content__stats {
  margin: 50px auto 0;
  width: 90%;
}

.about-content__stats ul {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.about-content__stats li {
  align-items: center;
  display: flex;
  flex: 1 0 30%;
  flex-flow: row nowrap;
  justify-content: center;
}

.about-content__stats li object {
  height: auto;
  width: 60px;
}

.about-content__stats li div {
  margin-bottom: 40px;
}

.about-content__stats li p {
  color: #F49206;
  font-size: 1.5em;
  padding-left: 20px;
}

.about-content__stats li p:first-child {
  font-size: 4em;
  margin-bottom: 10px;
}

.about-content__description {
  align-self: center;
  color: #232323;
  font-size: 1.8em;
  letter-spacing: 0.1em;
  line-height: 1.5em;
  margin: 50px auto;
  text-align: center;
  width: 59%;
}

.about-content__description span {
  background-color: #FFF;
}

.speakers {
  background-color: #808080;
  height: 800px;
}

.speakers-headliners {
  display: flex;
  flex-flow: row wrap;
}

.speakers-headliners > div {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1 0 50%;
  height: 50%;
}

.speakers-headliners > div:first-child {
  background-image: url("../img/1.jpg");
}

.speakers-headliners > div:nth-child(2) {
  background-image: url("../img/2.jpg");
}

.speakers-headliners > div:nth-child(3) {
  background-image: url("../img/3.jpg");
}

.speakers-headliners > div:nth-child(4) {
  background-image: url("../img/4.jpg");
}

.speakers-headliners > div:hover > .speakers-headliners--hover {
  opacity: 1;
}

.speakers-headliners > div .speakers-headliners--hover {
  background-color: rgba(68, 68, 68, 0.6);
  height: 100%;
  opacity: 0;
  position: relative;
  width: 100%;
  transition: all .1s ease-in-out;
}

.speakers-headliners > div .speakers-headliners--hover > p {
  color: #FFF;
  letter-spacing: 0.08em;
  position: absolute;
  right: 10px;
}

.speakers-headliners > div .speakers-headliners--hover > p span {
  color: #F49206;
}

.speakers-headliners > div .speakers-headliners--hover .speaker_describe1 {
  top: 20px;
}

.speakers-headliners > div .speakers-headliners--hover .speaker_describe2 {
  top: 60px;
}

.speakers-headliners > div .speakers-headliners--hover .speaker_describe3 {
  top: 100px;
}

.speakers-headliners > div .speakers-headliners--hover .speaker__identify {
  bottom: 30px;
  left: 10px;
  position: absolute;
}

.speakers-headliners > div .speakers-headliners--hover .speaker__identify p {
  color: #F49206;
  font-size: 1.1em;
  letter-spacing: 0.05em;
  padding-left: 5px;
  position: relative;
}

.speakers-headliners > div .speakers-headliners--hover .speaker__identify p:first-child {
  color: #FFF;
  font-size: 2em;
  margin-bottom: 3px;
  padding-left: 0;
}

.schedule {
  background-color: #F49206;
}

.schedule-dates {
  display: flex;
  flex-flow: row nowrap;
  padding-bottom: 60px;
}

.schedule-dates__programme {
  bottom: 0;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  text-align: center;
}

.schedule-dates__programme h2 {
  border: 1px solid #232323;
  cursor: pointer;
  display: inline-block;
  padding: 10px;
}

.schedule-dates__programme h2:hover {
  background-color: #232323;
  color: #FFF;
}

.schedule-dates__column {
  flex: 1 0 33%;
}

.schedule-dates__column h1 {
  border-right: 8px solid #000;
  margin-bottom: 30px;
  padding: 20px 0 45px;
  text-align: center;
}

.schedule-dates__column .lesson {
  height: 320px;
  margin-bottom: 40px;
  padding: 9px;
  position: relative;
}

.schedule-dates__column .lesson h5 {
  border: 1px solid #FFF;
  color: #FFF;
  display: inline-block;
  letter-spacing: 0.06em;
  padding: 12px 14px;
}

.schedule-dates__column .lesson h4 {
  margin-top: 50px;
  text-align: center;
}

.schedule-dates__column .lesson span {
  padding-left: 8px;
}

.schedule-dates__column .lesson p {
  font-size: 1.5em;
  margin: 70px auto 0;
  text-align: center;
  width: 90%;
}

.schedule-dates__column .lesson.lecture {
  background-color: #EDAB18;
}

.schedule-dates__column .lesson.workshop {
  background-color: #D12424;
}

.schedule-dates__column .lesson.case {
  background-color: #F25303;
}

.schedule-dates__column:last-child h1 {
  border: 0;
}

.schedule-dates__column:last-child .lesson {
  height: 250px;
}

.schedule-dates__column:last-child .lesson:last-child {
  height: 180px;
}

.schedule-dates__column:last-child p {
  margin-top: 30px;
}

.register {
  background-color: #232323;
  min-height: 800px;
  height: 800px;
}

.register-outline {
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  padding: 4em 8px;
}

.register-outline__title {
  color: #FFF;
  flex: 1 0 30%;
  text-transform: uppercase;
}

.register-outline__title span {
  color: #F49206;
  font-size: 70%;
  vertical-align: super;
}

.register-outline__form {
  align-items: center;
  align-self: center;
  display: flex;
  flex: 1 0 60%;
  justify-content: center;
}

.register-outline__form form {
  font-family: "Audiowide", serif;
  max-width: 500px;
  min-width: 250px;
  width: 60%;
}

.register-outline__form input {
  border: 1px solid rgba(35, 35, 35, 0.2);
  border-radius: 2px;
  height: 34px;
  margin: 10px 0 20px;
  outline: 0;
  padding: 2px 10px;
  width: 100%;
}

.register-outline__form label {
  color: #F49206;
  font-size: 1em;
}

.register-outline__form button {
  background-color: #F49206;
  border: 0;
  color: #FFF;
  display: block;
  font-family: "Audiowide", serif;
  font-size: 1.3em;
  height: 50px;
  margin: 30px auto 0;
  outline: 0;
  width: 70%;
  transition: all .2s ease-in;
  box-shadow: 0px 10px 0 0 #5f3902;
  transform: translateY(0px);
}

.register-outline__form button:active {
  box-shadow: 0px 0px 0 0 #5f3902;
  transform: translateY(10px);
}

.contact {
  height: 60vh;
  max-height: 800px;
}

.contact-details {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
}

.contact-details__address {
  align-items: center;
  display: flex;
  flex: 1 0 40%;
  flex-flow: column nowrap;
  margin-top: 7em;
  min-width: 250px;
  text-align: center;
  height: 80%;
}

.contact-details__address > div {
  height: 50%;
  margin-bottom: 40px;
}

.contact-details__address > div h2 {
  border-bottom: 4px solid #F49206;
  display: inline-block;
  margin-bottom: 20%;
  padding-bottom: 5px;
}

.contact-details__address > div p {
  font-size: 1.1em;
  padding: 5px 0;
}

.contact-details__address > div i {
  cursor: pointer;
  font-size: 1.5em;
  padding: 10px;
  transition: all 0.2s ease-in;
}

.contact-details__address > div i:hover {
  color: #F49206;
}

.contact-details__map {
  flex: 1 0 60%;
  height: 100%;
}

.contact-details__map #map {
  height: 100%;
  min-height: 300px;
  width: 100%;
}

footer ul {
  margin-top: 10px;
  text-align: center;
}

footer li {
  cursor: pointer;
  display: inline-block;
  font-size: 0.8em;
  padding: 30px 5%;
}

footer li:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1024px) {
  body {
    font-size: 90%;
  }
  .intro-info {
    align-items: flex-start;
    padding-top: 20px;
  }
}

@media screen and (max-width: 669px) {
  body {
    font-size: 70%;
  }
  .about-content__stats li {
    flex: 1 0 100%;
    margin: 20px 0;
  }
  .about-content__description {
    text-align: justify;
    width: 90%;
  }
  .speakers {
    height: auto;
  }
  .speakers-headliners--hover {
